
.info_container{
    margin-top: 20px;
}

.info_title{
    font-size: 18px;
    font-weight: bold;
    padding-left: 50px;
}

.info_description_container{
    display: block;
    width: 300px;
    margin-left: auto;
    margin-right: auto;
    padding: 20px;
    text-align: justify;

    @media screen and(min-width:479px) {
        width: 450px;
    }
    
    @media screen and(min-width:767px) {
        width: 600px;
    }
}

.info_description{
    margin-top: 20px;
    font-size: 16px;
}

.info_date{
    padding-top: 40px;
    font-size: 14px;
}

.info_buttons{
    margin-top: 20px;
    padding-left: 50px;
}

.info_btn{
    width: 75px;
    height: 25px;
    &:not(:last-child){
        margin-right: 20px;
    }
}